<template>
	<div class="sub-container">
		<ul class="tabs sub">
			<li @click="goto('/admin/rolling-banner')">메인 배너</li>
			<li @click="goto('/admin/introduction')">운동친구 소개</li>
			<li @click="goto('/admin/admin-member')">운동친구 멤버</li>
			<li @click="goto('/admin/faq')" class="current">자주묻는질문</li>
			<li @click="goto('/admin/terms')">약관</li>
		</ul>

		<div class="contents-container">
			<!-- 운동 친구 소개 -->
			<p class="contents-title underline marB-20 body3">자주묻는질문 등록</p>
			<div class="input-container">
				<p class="small-title">카테고리</p>
				<SelectBoxAdmin
					class="small marB-20"
					:options="options"
					v-model="selected"
					placeholder="카테고리를 선택해주세요"
				/>

				<div class="input-item">
					<p class="small-title">제목(문의내용)</p>
					<input
						type="text"
						class="admin-input large"
						name="title"
						placeholder="타이틀"
						v-model="title"
					/>
				</div>

				<div class="input-item">
					<p class="small-title">내용(해결방법)</p>
					<textarea
						class="textarea marB_20"
						placeholder="내용을 입력해주세요"
						v-model="contents"
					>
					</textarea>
				</div>
				<button class="outlined-btn" type="button" @click="addFaq">
					등록
				</button>
			</div>
		</div>
	</div>
</template>

<script>
import SelectBoxAdmin from '@/components/common/SelectBoxAdmin';
import { addFaq } from '@/api/faq';
export default {
	components: { SelectBoxAdmin },
	data: () => ({
		options: [
			{ id: 'AC', name: '계정' },
			{ id: 'PY', name: '결제' },
			{ id: 'SH', name: '배송' },
			{ id: 'PD', name: '프로그램 및 굿즈' },
			{ id: 'HP', name: '홈페이지 이용' },
			{ id: 'ETC', name: '기타' },
		],
		selected: {},
		title: '',
		contents: '',
	}),
	methods: {
		goto(url) {
			this.$router.push({ path: url });
		},
		async addFaq() {
			if (this.isEmptyObject(this.selected)) {
				this.errMsg = '카테고리를 선택해주세요';
				alert(this.errMsg);
				return false;
			}
			if (!this.title) {
				this.errMsg = '제목을 입력해주세요';
				alert(this.errMsg);
				return false;
			}
			if (!this.contents) {
				this.errMsg = '내용을 입력해주세요';
				alert(this.errMsg);
				return false;
			}
			let data = {
				categoryId: this.selected,
				title: this.title,
				contents: this.contents,
			};
			console.log(data);
			try {
				let result = await addFaq(data);
				if (result.status == 200) {
					alert('자주 묻는 질문이 등록되었습니다');
				} else {
					alert('에러가 발생했습니다');
				}
			} catch (e) {
				alert('에러가 발생했습니다');
			}
			await this.initForm();
		},
		initForm() {
			this.title = '';
			this.contents = '';
		},
		isEmptyObject(param) {
			return Object.keys(param).length === 0 && param.constructor === Object;
		},
	},
};
</script>
